import * as React from 'react'
import './App.css'
import Box from '@mui/material/Box'
import { TextField } from '@mui/material'
import PlayCircleFilledWhiteIcon from '@mui/icons-material/PlayCircleFilledWhite'
import PauseCircleIcon from '@mui/icons-material/PauseCircle'
import RestartAltIcon from '@mui/icons-material/RestartAlt'
import BackspaceOutlinedIcon from '@mui/icons-material/BackspaceOutlined'
import PlayCircleOutlineIcon from '@mui/icons-material/PlayCircleOutline'
import Fab from '@mui/material/Fab'
import $ from 'jquery'
import Stack from '@mui/material/Stack'
import ToggleButton from '@mui/material/ToggleButton'
import ToggleButtonGroup from '@mui/material/ToggleButtonGroup'
import LooksOneOutlinedIcon from '@mui/icons-material/LooksOneOutlined'
import LooksTwoOutlinedIcon from '@mui/icons-material/LooksTwoOutlined'
import Looks3OutlinedIcon from '@mui/icons-material/Looks3Outlined'
import Looks4OutlinedIcon from '@mui/icons-material/Looks4Outlined'
import Looks5OutlinedIcon from '@mui/icons-material/Looks5Outlined'

function App() {
	const [description, setDescription] = React.useState('')
	const [timerId, setTimerId] = React.useState([])
	const ref = React.useRef(null)
	const [speed, setSpeed] = React.useState(0)
	const [inProgress, setInProgress] = React.useState(false)
	const [savedSpeed, setSavedSpeed] = React.useState(0)

	const handleChangeDescription = (event) => {
		if (event.target.value === '') {
			setInProgress(false)
		}
		setDescription(event.target.value)
	}

	const handleChangeSpeed = (event, newSpeed) => {
		if (newSpeed !== null) {
			setSpeed(+newSpeed)
			if (timerId.length) {
				timerId.forEach((id) => clearInterval(id))
				setTimerId([])
			}
			start(+newSpeed)
		}
	}

	const setResult = (str) => {
		ref.current.innerHTML = str
			.split('\n')
			.map((item) => {
				if (item === '') {
					return '<br />'
				}
				return `<p>${item}</p>`
			})
			.join('')
	}

	const continueGame = () => {
		if (timerId.length < 1 && description) {
			start(savedSpeed)
			setSpeed(savedSpeed)
		}
	}

	const clearGame = () => {
		if (timerId.length) {
			timerId.forEach((id) => clearInterval(id))
			setTimerId([])
		}
		setInProgress(false)
		setResult('')
		setDescription('')
		setSavedSpeed(0)
		setSpeed(0)
	}

	const restartGame = () => {
		setInProgress(false)
		if (timerId.length) {
			timerId.forEach((id) => clearInterval(id))
			setTimerId([])
		}
		setResult(description)
		setSavedSpeed(0)
		setSpeed(0)
	}

	const stopGame = () => {
		if (timerId.length) {
			timerId.forEach((id) => clearInterval(id))
			setTimerId([])
		}
		setSavedSpeed(speed)
		setSpeed(0)
	}

	const startGame = () => {
		setInProgress(true)
		setResult(description)
		if (timerId.length < 1 && description) {
			start()
			setSpeed(1)
		}
	}

	const start = (speedValue = 1) => {
		let getTextNodesIn = function (el) {
			return $(el)
				.find(':not(iframe,script)')
				.addBack()
				.contents()
				.filter(function () {
					return this.nodeType === 3
				})
		}

		let textNodes = getTextNodesIn($('p'))

		let wordsInTextNodes = []
		for (let i = 0; i < textNodes.length; i++) {
			let node = textNodes[i]
			let words = []
			let re = /[a-zA-Zа-яА-Я]+/g
			let match
			while ((match = re.exec(node.nodeValue)) != null) {
				let word = match[0]
				let position = match.index
				words.push({
					length: word.length,
					position: position,
				})
			}
			wordsInTextNodes[i] = words
		}

		function messUpWords() {
			for (let i = 0; i < textNodes.length; i++) {
				let node = textNodes[i]
				for (let j = 0; j < wordsInTextNodes[i].length; j++) {
					if (Math.random() > 1 / 10) {
						continue
					}
					let wordMeta = wordsInTextNodes[i][j]
					let word = node.nodeValue.slice(wordMeta.position, wordMeta.position + wordMeta.length)
					let before = node.nodeValue.slice(0, wordMeta.position)
					let after = node.nodeValue.slice(wordMeta.position + wordMeta.length)

					node.nodeValue = before + messUpWord(word) + after
				}
			}
		}

		function messUpWord(word) {
			if (word.length < 3) {
				return word
			}

			return word[0] + messUpMessyPart(word.slice(1, -1)) + word[word.length - 1]
		}

		function messUpMessyPart(messyPart) {
			if (messyPart.length < 2) {
				return messyPart
			}

			let a, b
			while (!(a < b)) {
				a = getRandomInt(0, messyPart.length - 1)
				b = getRandomInt(0, messyPart.length - 1)
			}

			return messyPart.slice(0, a) + messyPart[b] + messyPart.slice(a + 1, b) + messyPart[a] + messyPart.slice(b + 1)
		}

		function getRandomInt(min, max) {
			return Math.floor(Math.random() * (max - min + 1) + min)
		}
		setTimerId([...timerId, setInterval(messUpWords, 100 / speedValue / 2)])
	}

	return (
		<div className='App'>
			<div className='container'>
				<div className='text'>
					<Box
						sx={{
							'.css-1u3bzj6-MuiFormControl-root-MuiTextField-root': { width: '100%' },
							'.css-dpjnhs-MuiInputBase-root-MuiOutlinedInput-root': {
								p: '10px',
								pb: '30px',
								pt: '15px',
								'& > #outlined-textarea': {
									'&::-webkit-scrollbar': { width: 5, backgroundColor: '#c4c5f8' },
									'&::-webkit-scrollbar-thumb': { backgroundColor: '#1976d2' },
								},
								'MuiFormControl-root MuiTextField-root css-i44wyl': { width: '100%' },
							},
						}}>
						<div>
							<TextField
								id='outlined-textarea'
								value={description}
								placeholder='Напиши любой текст'
								minRows={5}
								// maxRows={20}
								multiline
								onChange={(e) => handleChangeDescription(e)}
							/>
						</div>
					</Box>
				</div>
				<div className='btns'>
					<Stack direction='column' spacing={1}>
						{speed === 0 && !inProgress ? (
							<Fab variant='extended' size='small' color='primary' onClick={() => startGame()} disabled={description === ''}>
								<PlayCircleOutlineIcon sx={{ mr: 1 }} />
								Начать
							</Fab>
						) : speed === 0 && inProgress ? (
							<Fab variant='extended' size='small' color='primary' onClick={() => continueGame()}>
								<PlayCircleFilledWhiteIcon sx={{ mr: 1 }} />
								Продолжить
							</Fab>
						) : (
							<Fab variant='extended' size='small' color='primary' onClick={() => stopGame()} disabled={speed === 0}>
								<PauseCircleIcon sx={{ mr: 1 }} />
								Остановть
							</Fab>
						)}
						<Fab variant='extended' size='small' color='primary' onClick={() => restartGame()} disabled={!description}>
							<RestartAltIcon sx={{ mr: 1 }} />
							Перезапустить
						</Fab>
						<Fab variant='extended' size='small' color='primary' onClick={() => clearGame()} disabled={description === ''}>
							<BackspaceOutlinedIcon sx={{ mr: 1 }} />
							Очистить
						</Fab>
						<ToggleButtonGroup size='small' value={`${speed === 0 ? savedSpeed : speed}`} color='primary' exclusive onChange={handleChangeSpeed} aria-label='text alignment'>
							<ToggleButton value='1' aria-label='left aligned' disabled={speed === 0}>
								<LooksOneOutlinedIcon />
							</ToggleButton>
							<ToggleButton value='2' aria-label='centered' disabled={speed === 0}>
								<LooksTwoOutlinedIcon />
							</ToggleButton>
							<ToggleButton value='3' aria-label='centered' disabled={speed === 0}>
								<Looks3OutlinedIcon />
							</ToggleButton>
							<ToggleButton value='4' aria-label='centered' disabled={speed === 0}>
								<Looks4OutlinedIcon />
							</ToggleButton>
							<ToggleButton value='5' aria-label='right aligned' disabled={speed === 0}>
								<Looks5OutlinedIcon />
							</ToggleButton>
						</ToggleButtonGroup>
					</Stack>
				</div>
				<div className='result' ref={ref}></div>
			</div>
		</div>
	)
}

export default App
